.hero {
  background-color: var(--accent-bg-color);
  z-index: 1;
}

.hero-tittle {
  font-family: Titan One;
  font-style: normal;
  font-weight: 400;
  font-size: 26px;
  line-height: 1.15;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: var(--pre-title-color);
  max-width: 218px;
  margin-bottom: 20px;
  @media screen and (min-width: 768px) {
    max-width: 162px;
    font-size: 22px;
    line-height: 25px;
  }
  @media screen and (min-width: 1280px) {
    max-width: 277px;
    font-size: 38px;
    line-height: 44px;
    letter-spacing: 0.04em;
    margin-bottom: 25px;
  }
}

.tittle-span {
  color: var(--accent-secondary-color);
  font-size: 48px;
  @media screen and (min-width: 768px) {
    font-size: 34px;
    line-height: 31px;
  }
  @media screen and (min-width: 1280px) {
    max-width: 277px;
    font-size: 58px;
    line-height: 52px;
    letter-spacing: 0.04em;
  }
}

.hero-text {
  top: 193px;
  left: 20px;
  position: absolute;
  z-index: 5;
  @media screen and (min-width: 768px) {
    top: 102px;
    left: 34px;
  }
  // ===============================================
  @media screen and (min-width: 1280px) {
    top: 171px;
    left: 19px;
  }
  // =======================================================
}

.hero-container {
  position: relative;
  height: 502px;
  @media screen and (min-width: 768px) {
    height: 399px;
  }
  @media screen and (min-width: 1280px) {
    height: 665px;
  }
}

.hero-btn-1 {
  width: 62px;
  height: 24px;
  background-color: var(--accent-secondary-color);
  margin-right: 8px;
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
  @media screen and (min-width: 1280px) {
    width: 102px;
    height: 40px;
    margin-right: 15px;
  }
  &:hover,
  &:focus {
    background-color: #c7133e;
  }
}

.btn-link-1 {
  font-family: DM Sans;
  font-weight: 400;
  font-size: 8px;
  line-height: 10px;
  text-align: center;
  color: var(--pre-title-color);
  display: inline-block;
  @media screen and (min-width: 1280px) {
    font-size: 14px;
    line-height: 18px;
  }
}

.hero-btn-2 {
  width: 83px;
  height: 24px;
  background-color: var(--pre-title-color);
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
  @media screen and (min-width: 1280px) {
    width: 132px;
    height: 40px;
  }
  &:hover,
  &:focus {
    background-color: var(--button-bg-hover);
  }
}

.btn-link-cont {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
}

.btn-link-2 {
  color: var(--accent-secondary-color);
  font-family: DM Sans;
  font-weight: 400;
  font-size: 8px;
  line-height: 10px;
  display: inline-block;
  @media screen and (min-width: 1280px) {
    font-size: 14px;
    line-height: 18px;
  }
}

.hero-icecream {
  position: absolute;
  z-index: 2;
  top: 98px;
  right: 32px;
  width: 171px;
  @media screen and (min-width: 768px) {
    height: 336px;
    width: auto;
    top: 63px;
    left: 378px;
  }
  @media screen and (min-width: 1280px) {
    top: 108px;
    left: 500px;
    height: 557px;
    width: 236px;
  }
}

.hero-ellipse {
  position: absolute;
  z-index: 2;
  // ===========================================
  @media screen and (max-width: 767px) {
    top: 83px;
    right: 0;
    height: 344px;
    width: auto;
  }
  // ==========================================
  @media screen and (min-width: 768px) {
    height: 322px;
    width: auto;
    top: 39px;
    left: 297px;
  }
  @media screen and (min-width: 1280px) {
    top: 52px;
    left: 380px;
    height: 538px;
    width: 538px;
  }
}

.hero-milk {
  display: none;
  @media screen and (min-width: 768px) {
    display: flex;
    position: absolute;
    height: 126px;
    width: auto;
    top: 245px;
    left: 0;
    @media screen and (min-width: 1280px) {
      display: flex;
      position: absolute;
      height: 191px;
      width: auto;
      top: 429px;
      left: auto;
    }
  }
}

.hero-list {
  display: none;
  @media screen and (min-width: 768px) {
    display: block;
    position: absolute;
    top: 94px;
    right: 34px;
  }
  @media screen and (min-width: 1280px) {
    top: 156px;
    right: 18px;
  }
}

.hero-item + .hero-item {
  margin-top: 13px;
  @media screen and (min-width: 1280px) {
    margin-top: 15px;
  }
}

.hero-number {
  font-family: Titan One;
  font-weight: 400;
  font-size: 28px;
  line-height: 1.14;
  text-align: right;
  letter-spacing: 0.04em;
  color: var(--accent-secondary-color);
  position: relative;
  z-index: 1;
  &::before {
    content: '';
    display: block;
    width: 16px;
    height: 16px;
    border-radius: 50%;
    background: white;
    position: absolute;
    top: -2px;
    right: -10px;
    z-index: -1;
  }
  @media screen and (min-width: 1280px) {
    font-size: 46px;
    line-height: 1.15;
    &::before {
      width: 30px;
      height: 30px;
      top: -7px;
      right: -15px;
    }
  }
}

.hero-text-right {
  font-weight: 400;
  font-size: 8px;
  line-height: 1.25;
  text-align: right;
  color: white;
  @media screen and (min-width: 1280px) {
    font-size: 14px;
    line-height: 1.28;
  }
}

.hero-woman {
  @media screen and (max-width: 767px) {
    display: none;
  }
  position: absolute;
  z-index: 20;
  width: 168px;
  height: 133px;
  top: 266px;
  left: 566px;
  @media screen and (min-width: 1280px) {
    position: absolute;
    width: 293px;
    height: 232px;
    top: 433px;
    left: 800px;
  }
}

.hero-text-and-btn {
  position: absolute;
  width: 114px;
  top: 292px;
  left: 154px;
  margin-left: -4px;
  background-color: inherit;
  @media screen and (max-width: 767px) {
    display: none;
  }
  @media screen and (min-width: 1280px) {
    width: 200px;
    top: 485px;
    margin-left: 30px;
  }
  transform: rotateY(0deg);
  backface-visibility: hidden;
  transition: transform 500ms cubic-bezier(0.4, 0, 0.2, 1);
  &.flipped {
    transform: rotateY(-180deg);
  }
}

.hero-lorem {
  max-width: 114px;
  font-weight: 400;
  font-size: 8px;
  line-height: 12px;
  color: #fff;
  margin-bottom: 7px;
  @media screen and (min-width: 1280px) {
    max-width: 200px;
    font-size: 14px;
    line-height: 20px;
    margin-bottom: 20px;
  }
}

.hero-label {
  width: 18px;
  height: 18px;
  position: relative;
  padding: 0;
  display: block;
  background-color: var(--pre-title-color);
  border-radius: 50%;
  border-color: transparent;
  cursor: pointer;
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
  @media screen and (min-width: 1280px) {
    width: 26px;
    height: 26px;
  }
}

.hero-label::after {
  background-image: url('../images/arrow-right.svg');
  content: '';
  stroke: red;
  background-repeat: no-repeat;
  background-size: contain;
  width: 9px;
  height: 9px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
