.contacts-section {
  padding-top: 86px;
  padding-bottom: 113px;
  background-image: linear-gradient(to bottom, #ffff 10%, #fff5f6 10%);
  @media screen and (min-width: 768px) {
    background-image: linear-gradient(to bottom, #ffff 20%, #fff5f6 20%);
    padding-top: 119px;
    padding-bottom: 158px;
  }
  @media screen and (min-width: 1280px) {
    background-image: url('../images/sectionbg2-1x.png'),
      linear-gradient(to bottom, #ffff 0%, #fff5f6 0%);
    @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
      background-image: url('../images/sectionbg2-2x.png'),
        linear-gradient(to bottom, #ffff 0%, #fff5f6 0%);
    }
    padding-top: 132px;
    padding-bottom: 165px;
    background-repeat: no-repeat;
    background-position: 0 230px;
    background-size: contain;
  }
}

.contacts-container {
  padding-left: 50px;
  padding-right: 50px;
  @media screen and (min-width: 768px) {
    padding-left: 20px;
    padding-right: 20px;
  }
}

.contacts-list {
  display: flex;
  flex-direction: column;
  margin-bottom: 54px;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.86;
  letter-spacing: 0.04em;
  @media screen and (min-width: 768px) {
    display: grid;
    gap: 20px;
    grid-template-columns: repeat(3, 1fr);
    font-size: 14px;
    margin-bottom: 81px;
  }
  @media screen and (min-width: 1280px) {
    font-size: 16px;
    gap: 30px;
    margin-bottom: 86px;
  }
}

.contacts-list__item {
  padding: 43px 20px 43px 20px;
  margin: 0 auto;
  background: #ffffff;
  box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
  border-radius: 24px;
  margin-bottom: 20px;
  &:last-child {
    margin-bottom: 0;
  }
  @media screen and (min-width: 768px) {
    margin-bottom: 0;
    padding: 50px 30px 50px 30px;
  }
  @media screen and (min-width: 1280px) {
    padding: 50px 44px 60px 44px;
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
  }
  &:hover,
  &:focus {
    transform: scale(1.1);
  }
}

.contacts-list__title {
  margin-bottom: 25px;
  width: 80px;
  background: #f0d1a5;
  border-radius: 4px;
  text-align: center;
  color: #ffffff;
  @media screen and (min-width: 768px) {
    font-size: 16px;
    margin-bottom: 30px;
  }
}

.contacts-list__title-green {
  background-color: #c2e297;
  width: 130px;
}

.contacts-list__city {
  margin-bottom: 10px;
  color: #000000;
  @media screen and (min-width: 768px) {
    margin-bottom: 14px;
  }
  @media screen and (min-width: 1280px) {
    margin-bottom: 10px;
  }
}

.conacts-list__texx {
  line-height: calc(18 / 12);
  color: #907e82;
}

.contacts-list__list {
  padding-bottom: 25px;
  margin-bottom: 25px;
  border-bottom: 1px solid #e1e1e1;
  &:last-child {
    padding-bottom: 0;
    margin-bottom: 0;
    border-bottom: none;
  }
  @media screen and (min-width: 768px) {
    padding-bottom: 30px;
    margin-bottom: 30px;
  }
}

.contacts-item__work {
  margin-bottom: 10px;
  @media screen and (min-width: 768px) {
    margin-bottom: 16px;
  }
}

.contacts-list__days {
  color: #000000;
}

.contacts-list__hours {
  color: #ffa5ba;
}

.contacts-list__link {
  line-height: 1.5;
  color: #907e82;
}

.contacts-btn {
  display: block;
  padding: 0;
  padding: 0 15px;
  margin: 0 auto;
  margin-bottom: 10px;
  &:last-child {
    margin-bottom: 0;
  }
  width: 160px;
  height: 44px;
  background-color: #d41443;
  color: #ffffff;
  border-radius: 22px;
  border: transparent;
  font-weight: 700;
  font-size: 16px;
  line-height: calc(21 / 16);
  @media screen and (min-width: 768px) {
    margin: 0;
    margin-right: 15px;
    width: 190px;
  }
  @media screen and (min-width: 1280px) {
    margin: 0;
    margin-right: 15px;
    width: 200px;
    transition: transform 500ms cubic-bezier(0.4, 0, 0.2, 1);
    transition: background-color 500ms cubic-bezier(0.4, 0, 0.2, 1);
    transition: color 500ms cubic-bezier(0.4, 0, 0.2, 1);
  }
  &:hover,
  &:focus {
    transform: scale(1.1);
  }
}

.contacts-btn.contacts-btn-second {
  padding: 0 30px;
  background-color: #ffffff;
  color: #d41443;
  @media screen and (min-width: 768px) {
    width: 175px;
  }
  @media screen and (min-width: 1280px) {
    margin: 0;
    margin-right: 15px;
    width: 180px;
  }
  position: relative;
  transition: background-color 500ms cubic-bezier(0.4, 0, 0.2, 1);
  &:hover,
  &:focus {
    background-color: var(--button-bg-hover);
  }
}

.contacts-button-box {
  @media screen and (min-width: 768px) {
    display: flex;
    justify-content: center;
  }
}

