.contacts-btn-first {
  display: flex;
  align-items: center;
  justify-content: center;
}

.contacts-btn-icon {
  stroke: var(--pre-title-color);
}

.contacts-button-text {
  font-weight: 700;
  font-size: 16px;
  line-height: 1.31;
  margin-right: 14px;
}

.contacts-btn-icon-second {
  stroke: var(--accent-secondary-color);
}

.contacts-btn-second {
  display: flex;
  align-items: center;
  justify-content: center;
}
